@import url('https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700');
@import url('https://fonts.googleapis.com/css?family=Roboto+Slab:400,700');
@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400');

body {
  font-family: "Open Sans", serif;
  /* background: url("img/bg.jpg") center center no-repeat;
  background-size: contain; */
}

.wrapper {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  
}

.main {
  height: 90vh;
  background: #fff;
  -webkit-box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75);
          box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75);
  width: 80%;
  margin: 0 auto;
}

.title-container {
  height: 40vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
  color: #000;
  /* background: url("img/bg.jpg") center center no-repeat;
  background-size: cover; */
  background-color: #4682B4;
}

.title-container__title {
  font-size: 50px;
  letter-spacing: 2px;
  line-height: 1.3;
  font-family: 'Roboto Slab', serif;
}

.title-container__subtitle {
  font-style: italic;
  font-weight: 100;
  letter-spacing: 1px;
  line-height: 1.5;
  font-family: "Merriweather", serif;
}

.form-container {
  background-color: #4682B4;
  height: 80vh;
  padding-top: 100px;
  padding-left: 50px;
  background: url("img/bg.jpg") center center no-repeat;
  background-size: cover;
}

input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 1px #4682B4;
  width: 30%;
  padding-bottom: 4px;
  color: #black !important;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
}

input[type="text"] { 
    outline: none;
}

input:-webkit-autofill {
    -webkit-text-fill-color: black;
    -webkit-text-fill-color: black !important;
}

button {
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #4682B4;
  color: #fff;
  font-weight: 100;
}

button:active {
  outline: none;
}

.weather__info {
  width: 60%;
  font-size: 20px;
  font-weight: 400;
  letter-spacing: 2px;
}

.weather__key {
  color: #4682B4;
  padding: 20px 0 20px 0;
  font-weight: 600;
}

.weather__key:last-child {
  border: 0;
}

.weather__value {
  color: #black;
  font-weight: 400;
}

.weather__error {
  color: #4682B4;
  font-size: 20px;
  letter-spacing: 1px;
  font-weight: 400;
}


